    
    *{
        margin: 0;padding: 0;
    }
    .App{
        text-align:center;
        background-color:black;
        height:100vh;
   
        }
     /*Slider-css*/   
    .container{
        position:relative;
        height:100%;
        
        
        }
        
        .description{
       
         position:absolute;
        z-index:999;
        color:white;
        top:20%;
        left:10%;
        }
        .des-order{
            display: flex;
            flex-direction: column;
            justify-content: left;
            align-items: start;
            align-items: start;
        }
        .description h2{
        font-size:46px;
        }
        
        .description p{
        font-size:26px;
        }
        .dot-slide{
            display: flex;justify-content:
             center;align-items: center;position: 
             absolute;
             bottom: 20px;
             left: 130px;
        
        }
        .span-dot{
            width: 20px;
            height: 10px;
            background-color: rgb(219, 194, 243);
            z-index: 10000;
            margin-left: 10px;  
            border-radius: 50px;
            transition: .3s ease-in-out;
        }
        .active{
            background-color: rgb(103, 9, 181);
            width: 39px;
        }